.page {
  height: 100%;
  overflow: hidden;
}
.page__header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 11;
  height: 10rem;
}
@media screen and (max-width: 920px) {
  .page__fade-in {
    position: fixed;
    opacity: 0;
    right: 0;
    top: 10rem;
    z-index: 19;
    transform: translate3d(300px, 0, 0);
    padding: 1.5rem 3rem;
    border: 1px rgb(85, 85, 255);
    border-style: none none solid solid;
    border-radius: 0 0 0 6px;
    box-shadow: 0 1px 10px #000;
    visibility: hidden;
    transition: transform 0.5s, opacity 0.5s ease-in-out;
  }
  .page__fade-in._active {
    display: flex;
    transform: translate3d(0, 0, 0);
    opacity: 1;
    visibility: visible;
  }
  .page__fade-in.with-border {
    background: linear-gradient(to right, #271e4d 60%, #1a1530);
  }
}
.page__main-content {
  position: relative;
  inset: 0;
  height: 100%;
  width: 100%;
  z-index: 8;
}

.main-content__about {
  position: relative;
  width: 100%;
  min-height: calc(100vh - 2px);
}
.main-content__about::after {
  z-index: 9;
  width: 100%;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  transition: all 0.7s ease-in-out;
}
.main-content__achievements {
  position: relative;
  height: 100%;
}
.main-content__footer {
  position: relative;
  z-index: 9;
}

.about {
  position: relative;
  width: 100%;
  height: 100%;
}
.about__bg-image {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
}
.about__bg-image img {
  opacity: 0.4;
  object-position: left;
  object-fit: cover;
}
.about__container {
  position: relative;
  top: 10rem;
  height: calc(100vh - 10rem);
}
.about__content {
  padding: 4.5rem 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
}

.content-body__column {
  flex: 0 0 100%;
  display: inline-flex;
  justify-content: flex-end;
}
.content-body__text {
  text-shadow: 1px 1px 2px #000;
  max-width: 670px;
  color: #dfdfdf;
}
@media (min-width: 490.99px) {
  .content-body__text {
    text-align: justify;
  }
}
.content-body__text p {
  position: relative;
  margin-bottom: 2rem;
  display: inline-block;
  color: #fff;
  line-height: 170%;
  letter-spacing: 2px;
  font-weight: 300;
  font-size: 1.5rem;
}
.content-body__text span {
  display: block;
  line-height: 200%;
  font-size: 1.4rem;
  font-weight: 200;
  color: #d4d4d4;
}
.content-body__text span:not(:last-child) {
  margin-bottom: 1rem;
}
.content-body__text.hidden .content-body__ellipsis {
  transform: rotate(360deg);
}
.content-body__icon {
  transition: transform 0.9s ease-in-out;
  display: inline-flex !important;
  align-items: center;
  justify-content: center;
  height: 2rem;
  width: 3rem;
}
.content-body__hide-box {
  overflow: hidden;
  display: grid;
  grid-template-rows: 0fr;
  opacity: 0;
  transition: grid-template-rows 0.5s, opacity 0.9s ease-in-out;
}
@media (min-width: 920.99px) {
  .content-body__hide-box {
    grid-template-rows: 1fr;
    opacity: 1;
  }
}
.content-body__hide-box.hidden {
  grid-template-rows: 1fr;
  opacity: 1;
}
.content-body__wrapper {
  min-height: 0;
}
.content-body__title {
  text-transform: uppercase;
  color: rgb(204, 255, 0);
  padding-bottom: 2rem;
}
.content-body__ellipsis {
  cursor: pointer;
  display: inline-block !important;
  position: relative;
  width: 4px;
  height: 4px;
  line-height: 4px;
  background: rgb(204, 255, 0);
  border-radius: 50%;
  transition: 0.3s all ease;
}
@media (min-width: 920.99px) {
  .content-body__ellipsis {
    display: none !important;
  }
}
.content-body__ellipsis::before, .content-body__ellipsis::after {
  content: "";
  position: absolute;
  width: 4px;
  height: 4px;
  text-align: center;
  background: rgb(204, 255, 0);
  left: 50%;
  border-radius: 50%;
  transition: 0.4s all ease;
}
.content-body__ellipsis::before {
  top: 50%;
  left: -100%;
  transform: translate(0, -50%);
  bottom: 100%;
  margin-left: -3px;
}
.content-body__ellipsis::after {
  bottom: -50%;
  left: 100%;
  margin-left: 3px;
  transform: translate(0, -50%);
}

